.App {
  display: flex;
  flex-direction: column;
  text-align: center;
  height: 100vh;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.Main {
  height: 100%;
  display: flex;
}
.Headerbar {
  display: flex;
  border-bottom: 1px solid black;
  padding: 10px;
  text-align: left;
}
.HeaderButton {
  height: 100%;
  padding-left: 5px;
  padding-right: 5px;
  border-left: 1px solid black;
}
.HeaderButton:hover {
  background: lightgray;
}
.Sidebar {
  width:20%;
  height: 100%;
  border-right: 1px solid black;
}
.sidebarelement {
  text-align: left;
  padding: 10px;
  border-bottom: 1px solid black;
}
.sidebarelement:hover{
  background: lightgray;
}
.sidebarelement_selected {
  text-align: left;
  padding: 10px;
  border-bottom: 1px solid black;
  background: #e0e0e0;
}
.sidebarelement_selected:hover{
  background: lightgray;
}

.sidebarelement input {
  outline: none;
}

.Editor {
  margin: auto;
}

.MatrixLabel{
  margin-right: auto;
  text-align: left;
}
.MatrixCloseButton{
  color: red;
}
.MatrixTitleBar{
  display: flex;
  margin-left: 10px;
  margin-right: 10px;
  padding-left: 5px;
  padding-right: 5px;
  background: lightgrey;
}
.MatrixContainer {
  display: flex;
  flex-direction: column;
  margin: 10px;
  margin-top: 0px;
  background: black;
  box-shadow: 10px 10px 9px -6px rgba(0,0,0,0.24);
}

.RowContainer {
  display: flex;
  flex-direction: row;
}

.ledcontainer {
  width: 14px;
  height: 14px;
}

.LedOn {
  margin: 2px;
  width: 10px;
  height: 10px;
  background: white;
  border-radius: 50%;
}

.LedOff {
  margin: 2px;
  width: 10px;
  height: 10px;
  background: gray;
  border-radius: 50%;
}

.KaiborgRow{
  display: flex;
  width: 100%;
  margin-bottom: 10px;
}

.KaiborgRow_eye {
  justify-content: space-between;
}

.KaiborgRow_nose {
  justify-content: center;
}

.KaiborgRow_mouth {
  justify-content: space-around;
}

.ModalContainer {
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgb(0,0,0); /* Fallback color */
  background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
}
.ModalContent {
  background-color: #fefefe;
  margin: 15% auto; /* 15% from the top and centered */
  padding: 20px;
  border: 1px solid #888;
  width: 20%;
}